.input.primary {
  backdrop-filter: blur(12px);
  color: var(--base-content-100);
  font-size: 14px;
  font-weight: 400;
  font-family: var(--font-family-second);
  background: #ffffff0a;
  border: 1px solid #ffffff14;
  border-radius: 20px;
  padding: 20px;
  transition: all .2s;
}

.input.primary::placeholder {
  color: #fff6;
}

.input.primary:hover {
  border-color: #ff43104d;
}

.input.primary:focus {
  border-color: var(--orange-100);
}
